import React from 'react';

const Header = (props) => {
    const title = props.title;
    const subtitle = props.subtitle;

    return (
        <div>
        <h1>{title}</h1>
        <h2>{subtitle}</h2>
        </div>
    );
}

Header.defaultProps = {
    title: 'Indecision'
};

export default Header;